import ace from 'ace-builds/src-noconflict/ace';
import RenderManager from './rendermanager.js';
import Split from 'split.js';
import { h, Component, render, Fragment } from 'preact';
import { CodeEditor } from './codeeditor.js';
import { OutputBlock } from './outputblock.js';
import { TopNavBar } from './topnavbar.js';
import { TabGroup, Tab } from './utilcontrols/utilcontrols.js';
import { LogManager } from './logmanager.js';
import newFileTemplate from './etc/newfiletemplate.js';
import { pushFileDownload, requestFileUpload} from './utilcontrols/webfilehandling.js';
import { AboutModal } from './etc/aboutmodal.js';
import { AutoSaveManager } from './utilcontrols/autosavemanager.js';
import _ from 'underscore';

ace.config.set('basePath', 'ace-builds');

const INPUT_PANELS = {
  CODE: 'CODE',
  OPTIONS: 'OPTIONS',
  USER_PARAMS: 'USER_PARAMS'
};


class App extends Component {
  constructor(props) {
    super(props);
    // Don't call this.setState() here!
    this.renderManager = new RenderManager({
      renderWorker: new Worker('./renderWorker.js'),
    });

    this.autoSaveManager = new AutoSaveManager();

    this.newTemplate = newFileTemplate;
    this.logManager = new LogManager;

    this.editorSessions = {};

    this.state = {
      userCode: '',
      autoPreview: false,
      autoPreviewDelay: 3000,
      autoSave: true,
      autoSaveDelay: 3000,
      renderPreviewOutput: '',
      renderPreviewFormat: 'SVG',
      renderConsole: [],
      logMessages: [],
      activeInputPanel: INPUT_PANELS.CODE,
      showAboutForm: false
    }

    //Bind the event handlers
    this.handleUserCodeChange = this.handleUserCodeChange.bind(this);
    this.handleRequestRender = this.handleRequestRender.bind(this);
    this.handleChangeAutoPreview = this.handleChangeAutoPreview.bind(this);
    this.handleRenderDownloadRequest = this.handleRenderDownloadRequest.bind(this);
    this.handleCodeLoadRequest = this.handleCodeLoadRequest.bind(this);
    this.handleCodeSaveRequest = this.handleCodeSaveRequest.bind(this);
    this.handleSessionRegisterCallback = this.handleSessionRegisterCallback.bind(this);
    this.changeActiveInputPanel = this.changeActiveInputPanel.bind(this);
    this.pushUserCodeToEditor = this.pushUserCodeToEditor.bind(this);
    this.handleNewFileREquest = this.handleNewFileREquest.bind(this);

    this.handleLogUpdate = this.handleLogUpdate.bind(this);
    this.logManager.addUpdateListener(this.handleLogUpdate);

    this.handleAboutClose = this.handleAboutClose.bind(this);
    this.handleAboutShow = this.handleAboutShow.bind(this);
    this.handleNavCommand = this.handleNavCommand.bind(this);
  }

  autoSaveUserCode() {
    console.log('Auto save started');
    this.autoSaveManager.store(this.pullUserCodeFromEditor(INPUT_PANELS.CODE));
  }

  handleUserCodeChange(e) {
    //Don't run update if there is no change
    if (e != this.state.userCode) {
      this.setState({
        userCode: e
      });

      this.logManager.clear();

      this.renderManager.drawCode = e;
      if (this.state.autoPreview) {
        //Assume the delay was pushed down to the editor itself
        this.renderManager.requestRender()
      }



      if (this.state.autoSave) {
        this.autoSaveUserCodeDebounced();
      }
    }

  }



  handleRequestRender(format,returnAction) {
    this.logManager.clear();
    //TODO: update the code from state
    this.renderManager.drawCode = this.pullUserCodeFromEditor(INPUT_PANELS.CODE);
    try {
      let userParamsString = this.pullUserCodeFromEditor(INPUT_PANELS.USER_PARAMS)
      if ((userParamsString) && (userParamsString != '')) {
        this.renderManager.userParams = JSON.parse(userParamsString);
      }
    } catch (e) {
      this.logManager.error('Error Processing User Params',e);
    }

    //TODO: also pull the render params (don't assume they were committed);
    this.renderManager.requestRender(format,returnAction)
  }

  handleChangeAutoPreview(setAutoPreview) {
    this.setState({autoPreview : setAutoPreview})
  }

  handleLogUpdate(messages) {
    this.setState({
      logMessages : messages
    });
  }

  handleNavCommand(command) {
    console.log("Command request",command);
    switch(command) {
      case 'about': this.handleAboutShow();
    }
  }

  changeActiveInputPanel(newPanel) {
    //TODO: Fix this handle this switch properly
    console.log('Input panel change',newPanel);
    //preserve whatever is on the current panel - except if its the code panel as that is only ever hidden
    if (this.state.activeInputPanel == INPUT_PANELS.OPTIONS) {
      //Store the options
      let setOptions = this.editorSessions[INPUT_PANELS.OPTIONS].getValue();
      try {
        this.loadRenderOptionsFromJSON(setOptions);
      } catch (e) {
        this.logManager.error('Error processing JSON from Options',e);
      }
      //force the resulting options back into the codeEditor so it only shows valid code
      //TODO FIx this - for some reason this only works after the user clicks into the editor?
      // this.editorSessions[INPUT_PANELS.OPTIONS].setValue(this.renderOptionsToJSON());

    }

    if (this.state.activeInputPanel == INPUT_PANELS.USER_PARAMS) {
      //Store the user params
      let setParams = this.editorSessions[INPUT_PANELS.USER_PARAMS].getValue();

      try {
        this.loadUserParamsFromJSON(setParams);
      } catch (e) {
        this.logManager.error('Error processing JSON from User Params',e);
      }
    }

    this.setState({ activeInputPanel: newPanel })

  }

  handleRenderDownloadRequest(format) {
    console.log('Render Download Requested');
  }

  pushUserCodeToEditor(codeToLoad) {
    if (this.editorSessions[INPUT_PANELS.CODE]) {
      this.editorSessions[INPUT_PANELS.CODE].setValue(codeToLoad);
    }
  }

  pullUserCodeFromEditor(codePanel = INPUT_PANELS.CODE){
    if (this.editorSessions[codePanel]) {
      return this.editorSessions[codePanel].getValue();
    }
  }

  handleCodeLoadRequest() {
    console.log('Code load requested');
    requestFileUpload(this.pushUserCodeToEditor);
  }

  handleCodeSaveRequest(saveAs = false) {
    console.log('Code Save Requested');
    let userCode = this.pullUserCodeFromEditor(INPUT_PANELS.CODE);
    pushFileDownload('usercode.js',userCode,'application/json');
  }

  handleNewFileREquest() {
    console.log('New File Requested');
    this.pushUserCodeToEditor(this.newTemplate)
  }

  handleSessionRegisterCallback(editorName,editorSession) {
    this.editorSessions[editorName] = editorSession
  }

  renderOptionsToJSON() {
    console.log('Request render options in JSON');
    return this.renderManager.renderOptionsToJSON();
  }

  loadRenderOptionsFromJSON(newOptions) {
    this.renderManager.loadRenderOptionsFromJSON(newOptions);
  }

  handleAboutClose() {
    console.log('showAboutFormCose');
    this.setState({
      showAboutForm: false
    })
  }

  handleAboutShow() {
    this.setState({
      showAboutForm: true
    })
  }

  userParamsToJSON() {
    console.log('Request USer PArams for display');
    return '';

  }

  loadUserParamsFromJSON(newOptions) {
    // this.renderManager.loadRenderOptionsFromJSON(newOptions);
    console.log('USer Params load requested');
  }

	componentDidMount() {

    this.renderManager.on('renderStart',()=>{console.log('Render Starting')});
    this.renderManager.on('renderOutput',(data)=>{
      console.log('Render Output Received',data);

      if ((data.returnAction) && (data.returnAction == 'download')) {
        //extract just the data from the data URL;
        console.log(data.format,(data.format == 'PDF'))
        pushFileDownload(
          `RenderResult.${data.format}`,
          data.output,
          'application/' + (data.format=='PDF'?'PDF':'svg+xml'),
          (data.format == 'PDF')
        );

      } else {
        //Assume preview
        let renderPreviewFormat = data.format;
        let renderPreviewOutput = data.output;
        this.setState({
          renderPreviewFormat,
          renderPreviewOutput
        });
      }



    });
    this.renderManager.on('renderLog',(data)=>{console.log('Render Log',data.message)});
    this.renderManager.on('renderError',(data)=>{
      console.log('Render Error',data);
      this.logManager.error(data.message,`line: ${data.lineno}`);
    });
    this.renderManager.on('renderFinish',()=>{console.log('Render Complete')});


    Split(['#inputContainer','#outputContainer'], {
      gutterSize: 10,
      minSize: 100,
      onDragEnd: () => {
        //Update any loaded code editor sessions
        Object.values(this.editorSessions).forEach(session => {
          if (session) {
            session.resize();
          }
        });
      }
    });

    Split(['#renderOutputContainer','#errorsOutputContainer'], {
      sizes: [80,20],
      gutterSize: 10,
      minSize: [200,5],
      direction: 'vertical',
    });

    //debounce usercodechange function

    //Fetch any autosaved content
    //Setup auto save
    console.log('Setting auto save to:',this.state.autoSaveDelay);
    this.autoSaveUserCodeDebounced = _.debounce(this.autoSaveUserCode,this.state.autoSaveDelay || 15000);
    this.autoSaveManager.retrieve().then((autoSaveResponse) => {
      console.log('Auto save response',autoSaveResponse);
      if (autoSaveResponse) {
        // this.setState({userCode: autoSaveResponse});
        this.pushUserCodeToEditor(autoSaveResponse);
      }
    }).catch((e) => {
      console.log('Auto save response error',e);
    })

	}
	render(props, state) {
    // f = this.handleUserCodeChangeDebounced;
		return (
      h(Fragment,null,
        h(TopNavBar,{
          appTitle: 'LPT',
          activeInputPanel: state.activeInputPanel,
          onInputPanelChange: this.changeActiveInputPanel,
          onCodeLoadRequest: this.handleCodeLoadRequest,
          onCodeSaveRequest: this.handleCodeSaveRequest,
          onNewFileRequest: this.handleNewFileREquest,
          onNavCommand: this.handleNavCommand
        }),
  			h('div', {id:'appPrimary'},
          h('div', {id:'inputContainer'},
            h(CodeEditor,{
              onChangeValue: this.handleUserCodeChange,
              onChangeValueDebounce: state.autoPreviewDelay,
              initValue: this.newTemplate,
              sessionRegisterCallback: this.handleSessionRegisterCallback,
              name: INPUT_PANELS.CODE,
              id: 'userCodeEditor',
              hidden: (state.activeInputPanel!=INPUT_PANELS.CODE)
            }),
            //TODO: find a way to change this to conditional rendering and ensure the reset value is set properly
            h(CodeEditor,{
              initValue: this.renderOptionsToJSON(),
              sessionRegisterCallback: this.handleSessionRegisterCallback,
              name: INPUT_PANELS.OPTIONS,
              mode: 'json',
              id: 'optionsCodeEditor',
              hidden: (state.activeInputPanel!=INPUT_PANELS.OPTIONS)
            }),
            h(CodeEditor,{
              initValue: this.userParamsToJSON(),
              sessionRegisterCallback: this.handleSessionRegisterCallback,
              name: INPUT_PANELS.USER_PARAMS,
              mode: 'json',
              id: 'userParamsCodeEditor',
              hidden: (state.activeInputPanel!=INPUT_PANELS.USER_PARAMS)
            }),
          ),
          h(OutputBlock,{
            previewFormat: state.renderPreviewFormat,
            previewOutput: state.renderPreviewOutput,
            onRequestRender: this.handleRequestRender,
            autoPreview: state.autoPreview,
            onAutoPreviewChange: this.handleChangeAutoPreview,
            logMessages: state.logMessages,
          })
        ),
        h(AboutModal,{onClose: this.handleAboutClose, active: state.showAboutForm})
      )
		);
	}
}

const app = h(App, null, null);


render(app, document.getElementById('appContainer'));
