import { h, Component, render } from 'preact';
import { NavBar, NavBarItem, Button, CheckboxSwitch, Icon } from './utilcontrols/utilcontrols.js'

export class TopNavBar extends Component {
	constructor(props){
		super(props)
		this.state = {
			autoRefreshEnable: false
		}
	}
	componentDidMount() {

	}

	handleInputPanelTabClick(panelName) {
		this.props.onInputPanelChange(panelName);
	}

	handleNavCommand(command) {
		this.props.onNavCommand(command);
	}


	render(props, state) {
		return (
			h(NavBar,null,
        h('div',{class: 'title'},props.appTitle),
				h(NavBarItem,null,h('a',{onClick: this.handleInputPanelTabClick.bind(this,'CODE')},'Model')),
				h(NavBarItem,null,h('a',{onClick: this.handleInputPanelTabClick.bind(this,'OPTIONS')},'Options')),
				h(NavBarItem,null,h('a',{onClick: this.handleInputPanelTabClick.bind(this,'USER_PARAMS')},'User Params')),
				h(NavBarItem,null,h('a',{onClick: this.handleNavCommand.bind(this,'about')},'About')),
				h(NavBarItem,null,h('a',{onClick: props.onCodeSaveRequest},h(Icon,{icon: 'save_alt'}))),
				h(NavBarItem,null,h('a',{onClick: props.onCodeLoadRequest},h(Icon,{icon: 'upload_file'}))),
				h(NavBarItem,null,h('a',{onClick: props.onNewFileRequest},h(Icon,{icon: 'note'}))),
      )
		)
	}
}
